<template>
  <a-form-model
    ref="form"
    :model="entity"
    layout="vertical"
    autocomplete="off"
    :rules="rules"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <g-container-list :value.sync="entity.containerName">
          </g-container-list>
        </div>
      </template>
      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="展开面板">
            <template #content>
              <a-row :gutter="24">
                <a-col :span="4">
                  <!-- 创建人 -->
                  <a-form-model-item label="设备">
                    <g-select
                      v-model="entity.rescues"
                      @change="change"
                      :showSearch="true"
                      loadType="scroll"
                      :pagination="{ pageSize: 10 }"
                    />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </template>
          </g-collapse>
        </div>
      </template>
      <template #card-body>
        <g-collapse headerName="数据采集">
          <template #content>
            <g-data-points
              :dataPoints="entity.dataPoints"
              validDataName="dataPoints"
            >
            </g-data-points>
          </template>
        </g-collapse>
        <g-collapse headerName="工艺数据采集">
          <template #content>
            <g-data-points
              :dataPoints="entity.dataPoints"
              validDataName="dataPoints"
            >
            </g-data-points>  
          </template>
        </g-collapse>
        <g-collapse headerName="工艺参数卡">
          <template #content> 
            <g-data-points
              :dataPoints="entity.dataPoints"
              :type="'ppcDataItemList'"
            >
            </g-data-points>  
          </template>
        </g-collapse>
        <g-collapse headerName="操作描述">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="12">
                <a-form-item name="description" label="描述">
                  <a-textarea
                    rows="4"
                    name="description"
                    v-model:value="description"
                    placeholder="描述"
                    :max-length="1000"
                  ></a-textarea>
                </a-form-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </template>
      <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh()"></g-btn>
          <g-btn size="large" text="提交" type="primary" @callback="moveIn()">
          </g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>
import GSignModal from '@/components/g-sign-modal/g-sign-modal.vue';
import { createModal } from '@/utils';

export default {
  name: "MoveIn",

  data() {
    return {
      entity: {
        containerName: "",
        dataPoints: [
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Boolean",
          booleanTrue: '打赏',
          booleanFalse: '不打赏',
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Decimal",
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Float",
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Integer",
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Timestamp",
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "String",
          },
          {
            dataValue: "",
            dataPointName: "数据点",
            isRequired: true,
            dataType: "Fixed",
          },
        ],
      },
      rules: {},
      description: "",
    };
  },
  watch: {
    "entity.containerName": {
      handler() {
        this.$refs.form.validateField("containerName");
      },
    },
  },

  mounted() {},

  methods: {
    moveIn() {
      this.$refs.form.validate().then((valid) => {
        if (!valid) {
          return;
        }
        this.createSignModal();
      });
    },
    // 创建签名弹窗
    createSignModal() {
      createModal({
        title: '签名',
        width: '600px',
      },{
        component: GSignModal,
        success: (v) => {
          console.log(v);
        },
      },);
    },
    // 刷新
    refresh() {},
    // 改变
    change(v) {
      console.log(v);
    },
  },
};
</script>

<style lang="scss" scoped></style>
